.gallery {
  overflow: hidden;
}

.gallery__list {
  position: relative;

  img {
    width: 320px !important;
    height: 320px;

    object-fit: cover;
  }
}

@media (min-width: $media-breakpoint-lg) {
  .gallery__list {
    img {
      width: 360px !important;
      height: 360px;

      object-fit: cover;
    }
  }
}

@media (min-width: $media-breakpoint-xl) {
  .gallery__list {
  }
}
