.ecology {
  padding: 24px 10px 12px 18px;
  max-height: 620px;

  font-size: $largFontSize;
  font-weight: 300;
  line-height: $largLineHeght;
  letter-spacing: -0.05em;
  color: $white;

  background: url("../img/ecology-bg-2.png") no-repeat center center;

  overflow: hidden;

  p {
    margin-bottom: 21px;

    letter-spacing: -0.05em;
  }
}

@media (min-width: $media-breakpoint-lg) {
  .ecology {
    padding: 66px 30px 60px 44px;
    height: 620px;

    font-size: $largFontSize + 6;
    line-height: 36px;
  }
}

@media (min-width: $media-breakpoint-xl) {
  .ecology {
    padding: 84px 10px 60px 15px;

    font-size: $largFontSize + 6;
    line-height: 36px;
  }
}
