.about {
  padding-top: 1px;
  padding-bottom: 1px;

  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;

  background: radial-gradient(50% 50% at 50% 50%, #0092F2 27.6%, #006BE9 100%);

  img {
    position: relative;

    display: block;
  }

  p {
    margin-bottom: 21px;
  }
}

.about__wrapper {
  margin: 29px 17px;
}

.about__contacts {
  margin-top: 40px;
  max-width: 313px;
}

.about__text {
  margin: 0;
}

@media (min-width: $media-breakpoint-lg) {
  .about__wrapper {
    margin: 51px 20px 31px;
  }

  .about__contacts {
    margin-left: 12px;
  }

  .about__text {
    margin-top: 21px;
    margin-left: -31px;
    max-width: 550px;
  }
}

@media (min-width: $media-breakpoint-xl) {
  .about {
    img {
      position: relative;
      display: block;
      margin-left: -8px;
    }
  }
  .about__wrapper {
    margin: 51px 0 30px;
  }

  .about__contacts {
    margin-left: 2px;
  }

  .about__text {
    margin: 46px 0 0 49px;
    max-width: 100%;
  }
}
