.offers {
  margin: 65px 8px  28px 22px;

  font-size: 24px;
  font-weight: $baseFontWeight;
  line-height: 42px;
  letter-spacing: -0.05em;
}

.offers__list {
  margin-top: 42px;
  margin-left:6px;
  padding: 0;

  list-style:none;
}

.offers__item {
  &::before {
    content: "-";
    position: relative;
    left: -5px;
  }
}

@media (min-width: $media-breakpoint-lg) {
  .offers {
    margin: 128px 8px 129px 38px;
  }
}

@media (min-width: $media-breakpoint-xl) {
  .offers {
    margin: 173px 0 128px;
  }
}
