.structure {
  margin-top: 64px;

  font-size: 24px;
  font-weight: $baseFontWeight;
  line-height: 42px;
  letter-spacing: -0.05em;
}

.structure__header {
  margin-bottom: 174px;
}

.structure__title {
  margin-bottom: 43px;

  font-size: 36px;
  line-height: 42px;
  text-transform: uppercase;
}

.structure__view {
  color: $white;

  background: radial-gradient(50% 50% at 50% 50%, #0092F2 27.6%, #006BE9 100%);

  img {
    display: block;
  }
}

.structure__wrapper {
  flex-direction: column;
}

.structure__block-image {
  position: relative;

  min-height: 317px;
}

.structure__pack-svg {
  margin-top: 50%;
  margin-bottom: 16%;
  margin-left: 12px;
  width: 7%;
}

.structure__pack-image {
  position: absolute;
  top: -12px;
  left: 5px;

  width: 92%;
}

.structure__list {
  margin: 45px 0 30px 41px;
  padding: 0;
  width: 234px;

  font-size: $baseFontSize + 1;
  font-weight: $mediumFontWeight;
  line-height: $baseLineHeght + 1;
}

.structure__item {
  margin-bottom: 13px;

  letter-spacing: normal;
}

@media (min-width: $media-breakpoint-lg) {
  .structure {
    margin-top: 113px;
  }

  .structure__view {
    padding-bottom: 34px;
  }

  .structure__wrapper {
    position: relative;

    flex-direction: row;
  }
  .structure__block-image {
    width: 100%;
  }

  .structure__pack-svg {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 31px;
    padding-top: 226px;
    width: 92px;
  }

  .structure__pack-image {
    position: absolute;
    top: -20px;
    left: 37px;

    width: 741px;
  }

  .structure__list {
    margin: 94px 0 30px 0;
    width: 214px;

    font-size: $largFontSize;
    line-height: $largLineHeght;
    text-transform: uppercase;

    list-style: none;
  }

  .structure__item:not(:last-child) {
    margin-bottom: 95px;
  }
}

@media (min-width: $media-breakpoint-xl) {
  .structure {
    margin-top: 126px;
  }

  .structure__view {
    padding-bottom: 30px;
  }

  .structure__pack-svg {
    margin-left: 0;
    padding-top: 225px;
  }

  .structure__pack-image {
    margin-top: -14px;
    margin-left: 55px;
  }

  .structure__list {
    margin: 79px 0 30px 0;
    width: 406px;

    font-size: $largFontSize + 6;
    line-height: $largLineHeght + 7;

    list-style: none;
  }

  .structure__item:not(:last-child) {
    margin-bottom: 81px;
  }
}
