.pack {
  position: relative;

  background: radial-gradient(50% 50% at 50% 50%, #0092F2 27.6%, #006BE9 100%);
}

.pack__wrapper {
  margin-bottom: 211px;

  &--header {
    width: 100%;
    height: 544px;

    background: url("../img/pack-map.png") no-repeat 88% 15px;
  }
}

.pack__slogan {
  margin: 5px 0 0 5px;
  max-width: 300px;

  font-size: 48px;
  font-weight: $boldFontWeight;
  line-height: 56px;
  text-transform: uppercase;
  color: #00b107;

  span {
    display: inline-block;

    &:last-child {
      margin-top: 4px;
    }

    padding: 3px 20px 3px 15px;

    letter-spacing: -0.03em;

    background-color: $white;
  }
}

.pack__image {
  display: block;
  margin-top: 28px;
  margin-left: 10px;
  width: 281px;
}

.pack__text {
  margin: 28px auto;
  padding: 0 34px;
  min-width: 282px;

  font-size: $baseFontSize + 1;
  line-height: $baseLineHeght + 1;
  letter-spacing: -0.03em;
  color: $color-base;
}

@media (min-width: $media-breakpoint-lg) {
  .pack__wrapper {
    &--header {
      background-position-x: 98%;
    }
  }

  .pack__slogan {
    margin: 130px 0 0 10px;
    max-width: 380px;

    font-size: 64px;
    line-height: 75px;
  }

  .pack__image {
    position: absolute;
    top: 50px;
    left: 33%;

    width: 486px;
  }

  .pack__text {
    margin: 36px 0 0 10px;
    padding: 0;
    min-width: auto;
    max-width: 352px;
    max-height: 164px;

    font-size: $largFontSize;
    line-height: $largLineHeght;
    color: $white;

    overflow: auto;
  }
}

@media (min-width: $media-breakpoint-xl) {
  .pack__wrapper {
    &--header {
      background-position-x: right;
    }
  }

  .pack__slogan {
    margin-left: 0;
  }

  .pack__image {
    top: 38px;
    right: 32%;
    left: auto;

    width: 496px;
  }

  .pack__text {
    margin: 43px 0 0 0;
    max-width: 362px;
  }
}
